  width: 100px;
  height: 32px !important;
  line-height: 32px !important;
  text-align: center;
}

.btn-theme {
  background: #ff4f4c !important;
  color: #FFFFFF !important;
  border: none !important;
}

.dropdown-menu {
  width: 241px;
  transition: none;
  -moz-transition: none;
  -webkit-transition: none;
  -o-transition: none;
}

