main {
  .main-container {
    max-width: 1004px;
    margin: 0 auto;
    margin-bottom: 43px;
    // margin-top: 56px;

    .bread-crumbs {
      width: 100%;
      height: 48px;
      line-height: 48px;
      color: #404040;

      a {
        color: #404040;
      }
    }

    .main-panel-container {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      position: relative;

      .left-panel {
        width: 674px;
        display: flex;
        flex-direction: column;

        .blog-paginate {
          display: table;
          padding-left: 0;
          margin: auto;
          margin-top: 20px;
          border-radius: 4px;

          .pagination {
            height: 34px;
            display: inline-block;
            padding-left: 0;
            margin: 20px 0;
            border-radius: 4px;

            span {
              display: inline-block;
              border: 1px solid #ddd;
              padding: 6px 12px;
              color: #000;
              margin-left: -5px;
            }

            em {
              border: 1px solid #ff4f4c;
              padding: 6px 12px;
              background-color: #ff4f4c;
              color: #fff;
              margin-left: -5px;
              font-style: normal;
            }

            a {
              display: inline-block;
              border: 1px solid #ddd;
              padding: 6px 12px;
              color: #000;
              margin-left: -5px;
            }
          }
        }

        .artical-container {
          width: 100%;
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.06);
          background: #FFFFFF;
          margin-top: 10px;

          .artical-tab {
            width: 100%;
            height: 50px;
            padding: 0 12px;
            font-size: 15px;

            ul {
              border-bottom: 1px solid #F5F5F7;
              height: 100%;
              display: flex;
              justify-content: space-around;
              align-items: center;

              li {
                width: 60px;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                position: relative;

                a {
                  color: #404040;
                }

                .border-bottom {
                  display: none;
                }

                &.active {
                  a {
                    font-weight: 800;
                    color: #404040;
                  }

                  .border-bottom {
                    border-bottom: 2px solid #FF4F4C;
                    display: inline-block;
                    width: 24px;
                    position: absolute;
                    bottom: 0;
                    left: 18px;
                  }
                }
              }
            }
          }

          .artical-content {
            width: 100%;
            padding-top: 36px;

            .artical-item {
              width: 100%;
              height: 145px;
              display: flex;
              justify-content: space-between;
              border-bottom: 1px solid #F5F5F7;
              padding: 20px 12px;
              cursor: pointer;
              color: #404040;

              .item-pic {
                background: #ECECEC;

                img {
                  width: 140px;
                  height: 105px;
                }
              }

              .item-text {
                width: 484px;
                position: relative;

                .item-text-title {
                  width: 100%;
                  height: auto;
                  line-height: 28px;

                  h2 {
                    margin-top: 0;
                    margin-bottom: 0;
                    font-size: 20px;
                  }
                }

                .item-text-desc {
                  width: 100%;
                  font-size: 14px;
                  margin-top: 6px;
                  line-height: 20px;
                  min-height: 45px;
                  color: #9C9C9C;
                }

                .item-text-detail {
                  text-align: right;
                  width: 100%;
                  color: #9C9C9C;
                  font-size: 14px;
                  line-height: 20px;
                  color: #9C9C9C;
                  position: absolute;
                  bottom: -10px;
                  right: 0;
                }
              }

            }
          }
        }

        .swiper-container {
          width: 100%;
          height: 286px;
          position: relative;
          background: #ECECEC;

          margin: 0 auto;
          position: relative;
          overflow: hidden;
          z-index: 1;

          .swiper-pagination {
            left: auto;
            width: auto;
            right: 14px;
            bottom: 32px;

            .swiper-pagination-bullet {
              width: 6px;
              height: 6px;
              background: #D8D8D8;
              opacity: 1;
              margin: 0 5px;

              &.swiper-pagination-bullet-active {
                background: #ff4f4c !important;
              }
            }
          }

          #swiper-btn-left {
            position: absolute;
            width: 38.5px;
            height: 38.5px;
            background-image: url(https://files.wondercv.com/swiper-btn-left.png);
            background-size: 38.5px 38.5px;
            top: 50%;
            margin-top: -19px;
            left: 16.7px;
            z-index: 999;
          }

          #swiper-btn-right {
            position: absolute;
            width: 38.5px;
            height: 38.5px;
            background-image: url(https://files.wondercv.com/swiper-btn-right.png);
            background-size: 38.5px 38.5px;
            top: 50%;
            margin-top: -19px;
            right: 16.7px;
            z-index: 999;
          }

          .swiper-wrapper {
            width: 100%;
            height: 100%;

            .swiper-slide {
              width: 100%;
              height: 100%;

              img {
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }

      .right-panel {
        width: 320px;

        .hot-topic {
          padding: 0 16px;

          .hot-topic-content {
            .hot-topic-item {
              display: flex;
              width: 100%;
              padding-bottom: 10px;
              border-bottom: 1px solid #F5F5F7;
              margin-bottom: 16px;

              .item-pic {
                background: #ECECEC;
                width: 40px;

                img {
                  width: 40px;
                  height: 40px;
                  margin-right: 10px;
                }
              }

              .item-desc {
                h3 {
                  font-size: 14px;
                  margin-top: 0;
                  margin-bottom: 0;
                  margin-left: 10px;
                }

                a {
                  color: #404040;
                  line-height: 20px;
                }
              }
            }
          }
        }

        .right-fixed-1 {
          width: 320px;

          .right-fixed-2 {
            .resume-template {
              width: 100%;
              margin-top: 10px;
              background: #FFFFFF;
              border: 1px solid #F5F5F7;
              box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.06);

              .resume-template-title {
                width: 100%;
                padding: 16px;
                color: #404040;
                font-size: 14px;
              }

              .resume-template-content {
                padding: 0 12px;

                .resume-template-item {
                  border-bottom: 1px solid #F5F5F7;
                  margin-bottom: 16px;
                  position: relative;
                  height: 105px;

                  .item-desc {
                    color: #595959;
                    height: 52px;
                  }

                  &:hover {
                    .hover-masked {
                      position: absolute;
                      width: 307px;
                      height: 105px;
                      left: 50%;
                      margin-left: -153px;
                      display: flex;
                      background: #F5F5F7;
                      top: -10px;
                      justify-content: center;
                      align-items: center;
                    }
                  }

                  .hover-masked {
                    display: none;

                    a {
                      display: inline-block;
                      width: 100px;
                      height: 32px;
                      line-height: 32px;
                      text-align: center;
                      color: #FF4F4C;
                      background: inhert;
                      border-radius: 2px;
                      border: 1px solid #FF4F4C;
                    }

                    a:nth-child(1) {
                      margin-right: 10px;
                    }
                  }

                  .item-intro {
                    display: flex;

                    .item-intro-content {
                      margin-left: 10px;
                      margin-bottom: 12px;
                      width: 240px;
                      float: left;

                      .item-intro-title {
                        h3 {
                          color: #404040;
                          font-size: 14px;
                          margin-top: 0;
                          margin-bottom: 0;
                        }
                      }

                      .item-intro-num {
                        color: #9C9C9C;
                        font-size: 13px;
                      }
                    }

                    .item-intro-pic {
                      img {
                        width: 40px;
                        float: left;
                        margin-top: -4px;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .search-bar {
          width: 100%;
          position: relative;

          input {
            width: 100%;
            height: 32px;
            padding-right: 30px;
          }

          .search-btn {
            position: absolute;
            right: 5px;
            top: 5px;
            color: #B3B3B3;
          }
        }

        .success-case {
          .success-case-content {
            width: 100%;

            .case-desc {
              width: 290px;
              margin: 0 auto;
              color: #595959;
              margin-top: 20px;
              font-size: 14px;
            }

            .check-case {
              margin: 16px 0px 20px 16px;
            }

            .case-pic {
              img {
                width: 290px;
                height: 192px;
                margin: 0 auto;
                display: block;
              }
            }
          }
        }
      }
    }
  }
}

